import React from "react";
import { homeIcon, nextIcon } from "../../assets/icons/fontIcons";
import { Cell } from "../../components/cell/cell";
import { AppContext } from "../../components/context/appContext";
import { Fade } from "../../components/fade/fade";
import { Grid } from "../../components/grid/grid";
import { TactileButton } from "../../components/tactileButton/tactileButton";
import { initGrid, mapGridToHash } from "../../util/gameUtils";
import { useToggle } from "../../util/hooks";
import { applyCustomMarkup, joinClassesConditionally } from "../../util/utils";
import "./tutorialScreen.scss";

type TutorialText = string | JSX.Element;
type TutorialStepScript = {
    gridHash: string,
    text: TutorialText[],
    advanceCheck: (gridHash: string) => boolean,
};

const tutorialGridScript: TutorialStepScript[] = [
    {
        gridHash: "NNNN",
        text: [
            "This is an empty [b]cell[b].",
            "A [b]cell[b] is made of 4 quarters.",
            "[n]",
            "You can change the [c]color[c] of a quarter by [b]clicking[b] on it.",
            "[n]",
            "To advance, try changing the [c]color[c] of a quarter!",
        ],
        advanceCheck: gridHash => gridHash !== "NNNN",
    },
    {
        gridHash: "NNNN",
        text: ["All cells must be [c]colored[c]"],
        advanceCheck: gridHash => gridHash !== "NNN",
    },
];

export const TutorialScreen: React.FC = () => {
    const appContext = React.useContext(AppContext);

    const [tutorialStep, setTutorialStep] = React.useState(0);
    const [canAdvance, toggleCanAdvance] = useToggle(false);
    const [gridData, setGridData] = React.useState(initGrid(tutorialGridScript[0].gridHash))

    React.useEffect(
        () => {
            if (tutorialGridScript[tutorialStep].advanceCheck(mapGridToHash(gridData))) {
                if (!canAdvance) {
                    toggleCanAdvance();
                }
            } else {
                if (canAdvance) {
                    toggleCanAdvance();
                }
            }
        },
        [gridData],
    )

    React.useEffect(
        () => {
            setGridData(initGrid(tutorialGridScript[tutorialStep].gridHash))
        },
        [tutorialStep]
    )

    return <Fade>
        <div
            id="tutorial-screen"
            className={joinClassesConditionally([
                ["screen", true],
            ])}
        >

            <Grid height={1} width={1} gridData={gridData} setGridData={setGridData} />
            <div className="tutorial-text">
                {tutorialGridScript[tutorialStep].text.map(row => <span className="tutorial-text-row">{applyCustomMarkup(row.toString(), [["b", "bold"]])}</span>)}
            </div>


            <TactileButton
                className="advance-button"
                enabled={canAdvance}
                onClick={() => setTutorialStep(curr => curr + 1)}
                children={nextIcon}
            />
            <TactileButton
                className="home-button"
                onClick={() => appContext.setPage("home")}
                children={homeIcon}
            />
        </div>
    </Fade>
}